body {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  text-decoration: none;
  font-family: 'Roboto', sans-serif;
}

.app {
  min-height: 100vh;
  outline: none;
  display: flex;
}

.belt_active {
  height: 50px;
  background: rgb(64, 230, 130);
  width: 300px;
  border-radius: 8px;
  border: 5px solid transparent;
}

.belt_inactive {
  height: 50px;
  background: rgb(220, 59, 19);
  width: 300px;
  border-radius: 8px;
  border: 5px solid transparent;
}

.belt_block {
  display: flex;
  padding-top: 30px;
}

.belt_stoped {
  height: 50px;
  background: rgb(135, 133, 132);
  width: 300px;
  border-radius: 8px;
  border: 5px solid transparent;
}

.belt_main {
  padding-top: 3%;
  margin-left: 30px;
  min-width: 700px;
}

.belt_title {
  margin-left: 15px;
  padding-top: 14px;
}

.selected {
  border: 5px solid rgb(0, 13, 255)
}

.buttons_button {
  border: none;
  background: rgb(229, 236, 236);
  width: 600px;
  height: 70px;
  color: #000;
  border-radius: 8px;
  border: 2px solid rgb(171, 164, 164);
  font-family: 'Roboto', sans-serif;
  font-size: 20px;
  text-transform: uppercase;
  margin-top: 40px;
  outline: none;
  transition: transform 0.1s;
}

.buttons_button:hover {
  transform: scale(1.07);
}

.buttons_container {
  display: flex;
  flex-direction: column;
  margin-left: 7%;
}

.button_log {
  border: none;
  background: rgb(229, 236, 236);
  width: 300px;
  height: 70px;
  color: #000;
  border-radius: 8px;
  border: 2px solid rgb(171, 164, 164);
  font-family: 'Roboto', sans-serif;
  font-size: 20px;
  text-transform: uppercase;
  margin-top: 40px;
  outline: none;
  transition: transform 0.1s;
  margin-top: 17%;
  margin-left: 25%;
}

.button_log:hover {
  transform: scale(1.07);
}